.comparison {
	padding-top: toRem(70);
	padding-bottom: toRem(70);
	@media (max-width: $mobile) {
		padding-top: toRem(50);
		padding-bottom: toRem(40);
	}
	// .comparison__container

	&__container {
		display: flex;
		flex-direction: column;
	}

	&__header-top {
		&.content {
			.content__title {
				&::after {
					width: 12%;
					margin: toRem(15) auto 0 auto;
					@media (max-width: $mobile) {
						margin: toRem(10) auto 0 0;
						width: 50%;
					}
				}
				&:not(:last-child) {
					margin-bottom: toRem(15);
					@media (max-width: $mobile) {
						margin-bottom: toRem(10);
					}
				}
			}
			.content__text {
				max-width: toRem(765);
				margin: 0 auto;
				@media (max-width: $mobileSmall) {
					font-size: toRem(14);
				}
			}
			@media (max-width: $mobile) {
				text-align: left;
			}
		}
		&:not(:last-child) {
			margin-bottom: toRem(30);
		}
	}

	// .comparison__body

	&__body {
		background-color: var(--white-color);
		@media (min-width: $mobile) {
			padding: toRem(35) toRem(52);
			border-radius: toRem(20);
			border: toRem(1) solid rgba(0, 0, 0, 0.2);
			box-shadow: 0 0 toRem(33.4) 0 rgba(0, 0, 0, 0.05);
		}
		@media (max-width: $mobile) {
			margin: 0 toRem(-20);
			padding: toRem(20);
		}
	}

	// .comparison__header

	&__header {
		text-align: center;
		&:not(:last-child) {
			margin-bottom: toRem(40);
			@media (max-width: $mobile) {
				margin-bottom: toRem(21);
			}
		}
		&.content {
			.content__title {
				display: inline-block;
				@include adaptiveValue("font-size", 28, 18);
				&.title-decor {
					&::after {
						// content:'';
						margin-top: toRem(30);
						background-color: var(--yellow-color);
					}
				}
				@media (max-width: $mobile) {
					text-align: left;
					&.title-decor {
						&::after {
							display: none;
						}
					}
				}
			}
		}
	}

	&__wrapper {
		max-width: toRem(908);
		margin: 0 auto;
		display: flex;
		flex-direction: column;
		@media (max-width: $mobile) {
			display: none;
		}
	}

	// mobile version

	&__mobile {
		@media (min-width: $mobile) {
			display: none;
		}
		@media (max-width: $mobile) {
			display: grid;
			gap: toRem(7);
			// form {
			// 	margin-top: toRem(12);
			// }
		}
	}

	&__item {
		display: grid;
		grid-template-columns: 34% 1fr;
		gap: toRem(20);
		justify-content: start;
		padding: toRem(8) 0;
		align-items: start;
		&:not(:last-child) {
			border-bottom: toRem(1) solid rgba(0, 0, 0, 0.05);
		}
	}

	&__title {
		@media (max-width: $mobile) {
			font-size: toRem(16);
			font-weight: 500;
			color: var(--white-color);
			position: relative;
			z-index: 2;
		}
	}

	&__label {
	}

	//========================================================================================================================================================

	&__top {
		display: grid;
		grid-template-columns: 1fr toRem(172) 1fr;
		gap: toRem(25);
		justify-items: center;

		&:not(:last-child) {
			@media (min-width: $mobile) {
				margin-bottom: toRem(20);
			}
		}

		@media (max-width: $mobile) {
			grid-template-columns: 34% 1fr;
			gap: toRem(20);
			justify-items: start;
			padding: toRem(10) 0;
			position: relative;
			&:not(:last-child) {
				// margin-bottom: toRem(12);
			}
			&::before,
			&::after {
				content: "";
				position: absolute;
				width: 100vw;
				height: 100%;
				background-color: var(--brand-color);
			}
			&::before {
				left: 0;
			}
			&::after {
				right: 0;
			}
		}
	}

	&__label {
		display: flex !important;
		flex-direction: column;
		font-weight: 500;
		gap: toRem(10);
		letter-spacing: toRem(0.3);
		line-height: 1.3;
		display: inline-block;
		text-align: center;
		@include adaptiveValue("font-size", 24, 20, 0, 908, 768);
		&:not(:last-child) {
			// margin-bottom: toRem(28);
		}
		&.title-decor {
			&::after {
				margin-top: auto;
				height: toRem(2);
				background-color: var(--brand-color);
			}
		}
		@media (max-width: $mobile) {
			font-size: toRem(14);
			font-weight: 600;
			text-align: left;
			font-family: var(--primary-font);
		}
	}

	&__items {
		display: grid;
		gap: toRem(50);

		&:not(:last-child) {
			margin-bottom: toRem(45);
		}
		@media (max-width: $tablet) {
			gap: toRem(30);
		}

		@media (max-width: $mobile) {
			gap: toRem(0);
		}
	}

	// .comparison__items

	&__column {
		display: grid;
		grid-template-columns: 1fr toRem(172) 1fr;
		gap: toRem(25);
		text-align: center;
		align-items: center;
	}

	// .comparison__item

	&__value {
		letter-spacing: toRem(0.3);
		line-height: 1.65;
		&_orange {
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: var(--brand-color);
			font-weight: 600;
			color: var(--white-color);
			min-width: toRem(172);
			min-height: toRem(47);
			padding: toRem(10);
			clip-path: polygon(10% 0%, 90% 0%, 100% 50%, 90% 100%, 10% 100%, 0% 50%);
		}
		@media (max-width: $mobile) {
			font-size: toRem(14);
			line-height: normal;
		}
	}
	form {
		margin-top: toRem(20);
		@media (min-width: $mobile) {
			align-self: center;
		}
	}
}

//========================================================================================================================================================

.comparison-table {
	width: 100%;
	border-collapse: collapse;
	max-width: toRem(1064);
	margin: 0 auto;
	@media (max-width: $mobile) {
		display: none;
	}
}

.comparison-table th,
.comparison-table td {
	padding: toRem(10) toRem(30);
	text-align: left;
}

.comparison-table tbody {
	th:first-child,
	td:first-child {
		font-weight: 700;
		width: toRem(230);
	}
}
.comparison-table tbody tr {
	height: toRem(73);
	font-size: toRem(16);
	line-height: 1.65;
	&:nth-child(odd) {
		background-color: var(--green-color-500);
	}
	&:nth-child(even) {
		background-color: rgba(119, 203, 198, 0.4);
	}
}

thead {
}

.header-row {
	font-weight: 600;
	height: toRem(66);
	background-color: var(--green-color-300);
	font-size: toRem(22);
	color: var(--white-color);
}
