.pagination {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: toRem(20);
	position: relative;
	z-index: 5;
	margin-top: toRem(40);
	.swiper-pagination-bullet {
		flex: 0 0 toRem(10);
		width: toRem(10);
		height: toRem(10);
		border-radius: 50%;
		background-color: var(--brand-color);
		display: flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;
		&::before {
			content: "";
			flex: 0 0 toRem(21);
			width: toRem(21);
			height: toRem(21);
			border-radius: 50%;
			transition: all 0.3s;
			border: toRem(1) solid transparent;
			transition: border 0.3s;
		}
	}
	.swiper-pagination-bullet-active {
		&::before {
			border-color: var(--brand-color);
		}
	}
	@media (max-width: $mobileSmall) {
		margin-top: toRem(25);
	}
}
