.form {
	display: grid;
	gap: toRem(20);

	// .form__row

	// .form__line

	&__line {
	}

	&__label {
		display: block;
		font-size: toRem(16);
		margin-bottom: toRem(10);
	}

	// .form__input

	&__input {
	}

	// .form__send

	&__send {
		@media (min-width: $mobile) {
			margin-top: toRem(5);
		}

		&.button {
			color: var(--primary-color);
			@media (max-width: $mobile) {
				color: var(--white-color);
			}
			@media (max-width: $mobileSmall) {
				padding-top: toRem(13);
				padding-bottom: toRem(13);
				font-size: toRem(16);
			}
		}
	}
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}
.input {
	border-radius: toRem(10) !important;
	width: 100%;
	display: block;
	font-size: toRem(16);
	padding: 0 toRem(16);
	height: toRem(59);
	background-color: var(--white-color);
	transition: all 0.3s;
	color: var(--brand-color);
	outline: toRem(1) solid var(--brand-color);

	&:focus {
		outline-color: var(--brand-color);
	}
	@media (max-width: $mobileSmall) {
		height: toRem(43);
	}
}
