.faq {
	padding-top: toRem(60);
	padding-bottom: toRem(70);
	@media (max-width: $mobile) {
		padding-top: toRem(40);
		padding-bottom: toRem(40);
	}
	// .faq__container

	&__container {
		display: grid;
		align-items: start;
		gap: toRem(38);
		@media (min-width: $mobile) {
			grid-template-columns: 43.9% 1fr;
			align-items: center;
		}
		@media (max-width: $mobile) {
			gap: toRem(30);
		}
	}

	&__body {
		display: grid;
		gap: toRem(30);
		@media (max-width: $mobile) {
			order: 1;
			gap: toRem(15);
		}
	}

	// .faq__content

	&__content {
		&.content {
			.content__title {
				&::after {
					width: 23%;
					@media (max-width: $mobile) {
						display: none;
					}
				}
				&:not(:last-child) {
					@media (min-width: $mobile) {
						margin-bottom: toRem(22);
					}
				}
			}
			.content__text {
				@media (min-width: $mobile) {
					max-width: toRem(603);
					@include adaptiveValue("font-size", 20, 16, 0, $containerWidth, 767.98);
				}
				@media (max-width: $mobile) {
					display: none;
				}
			}
		}
	}

	&__spollers {
	}

	&__image {
		border-radius: toRem(20);
		overflow: hidden;
		padding-bottom: 117%;
		@media (max-width: $mobile) {
			padding-bottom: 83%;
			order: 2;
			border-radius: toRem(8);
			img {
				object-position: bottom;
			}
		}
	}
}

// .faq__spollers

.spollers {
	display: grid;
}
// Спойлер
.spollers__item {
	&:first-child {
		border-top: toRem(3) solid var(--white-light-color);
	}
	border-bottom: toRem(3) solid var(--white-light-color);
}
// Заголовок спойлера
.spollers__title {
	font-size: toRem(18);
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: toRem(18);
	width: 100%;
	cursor: pointer;
	text-align: left;
	padding: toRem(26) 0 toRem(26) 0;
	list-style: none;
	font-weight: 600;
	line-height: 1.3;
	transition: all 0.3s;
	@media (max-width: $mobileSmall) {
		font-size: toRem(14);
		padding: toRem(23) 0 toRem(23) 0;
	}
	@media (any-hover: hover) {
		&:hover {
			opacity: 0.6;
		}
	}
	&::marker,
	&::-webkit-details-marker {
		display: none;
	}

	&._spoller-active {
		opacity: 1;

		padding-bottom: toRem(12);

		.spollers__icon {
			transform: rotate(45deg);
		}
	}
}
.spollers__icon {
	flex: 0 0 toRem(12);
	width: toRem(12);
	height: toRem(12);
	transition: transform 0.3s;
	position: relative;
	top: toRem(6);
	transform-origin: 0 0;
	&::before,
	&::after {
		content: "";
		position: absolute;
		background-color: var(--brand-color);
		transform: translate(-50%, -50%);
		transition: all 0.3s;
	}
	&::before {
		width: 100%;
		height: toRem(1);
	}
	&::after {
		width: toRem(1);
		height: 100%;
	}
}

// Контент спойлера
.spollers__body {
	padding: toRem(0) toRem(0) toRem(25) toRem(0);
	line-height: 1.5;
	font-size: toRem(16);
	@media (max-width: $mobile) {
		padding-right: toRem(20);
	}
	@media (max-width: $mobileSmall) {
		font-size: toRem(12);
		padding-bottom: toRem(18);
	}
}
