body {
	// The scroll is blocked

	// The site is downloaded
	.loaded & {
	}
}
.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	@supports (overflow: clip) {
		overflow: clip;
	}
	// We press Footer
	> main {
		flex: 1 1 auto;
	}
	// Fix for sliders
	> * {
		min-width: 0;
	}
}

//========================================================================================================================================================
.page {
	// .page__hero

	&__hero {
	}
	&__about {
		padding-top: toRem(80);
		padding-bottom: toRem(80);
		@media (max-width: $mobile) {
			padding-top: toRem(50);
			padding-bottom: toRem(50);
		}
	}
	// .page__programs

	&__programs {
	}
}
//========================================================================================================================================================

.content {
	// .content__title

	&__title {
		@include adaptiveValue("font-size", 44, 24);
		font-weight: 700;
		line-height: 1.3;

		&::after {
			content: "";
			width: 30%;
			display: block;
			height: toRem(5);
			margin-top: toRem(10);
			border-radius: toRem(5);
			background-color: var(--brand-color);
		}
		&:not(:last-child) {
			margin-bottom: toRem(15);
		}
		@media (max-width: $mobileSmall) {
			&:not(:last-child) {
				margin-bottom: toRem(10);
			}
		}
		&_big {
			@include adaptiveValue("font-size", 47, 30);
		}
	}

	// .content__text

	&__text {
		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(14);
		}
	}

	// .content__button

	&__button {
	}
	&_center {
		text-align: center;
	}
	&_white {
		color: var(--white-color);
	}
	&_left {
		text-align: left;
	}
}

.text {
	line-height: 1.65;
	p {
		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
	}
}

.text-shadow {
	text-shadow: 0px 0px 0px rgba(0, 0, 0, 0.3);
}

.title-decor {
}

.ibg {
	position: relative;
	img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
	}
	&_contain {
		img {
			object-fit: contain;
		}
	}
}

.sr-only {
	position: absolute;
	width: toRem(1);
	height: toRem(1);
	padding: 0;
	margin: toRem(-1);
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border-width: 0;
}
