.advantages {
	padding-top: toRem(70);
	padding-bottom: toRem(100);
	@media (max-width: $mobile) {
		padding-top: toRem(50);
		padding-bottom: toRem(50);
	}
	background-color: var(--primary-color);
	// .advantages__container

	&__container {
	}

	// .advantages__content

	&__content {
		display: grid;
		gap: toRem(30);
		align-items: center;
		@media (min-width: $mobile) {
			grid-template-columns: 44.350158% 1fr;
		}
		@media (max-width: $mobile) {
			gap: toRem(10);
		}
		&:not(:last-child) {
			margin-bottom: toRem(57);
			@media (max-width: $mobileSmall) {
				margin-bottom: toRem(30);
			}
		}
		color: var(--white-color);
		&.content {
			.content__title {
				&.title-decor {
					@media (min-width: $mobileSmall) {
						&::after {
							display: none;
						}
					}
					@media (max-width: $mobileSmall) {
						&::after {
							width: 35%;
						}
					}
				}
			}

			.content__text {
				@media (min-width: $mobile) {
					max-width: toRem(475);
					margin-left: auto;
				}
				@media (max-width: $mobile) {
					text-align: right;
				}
			}
		}
	}

	// .advantages__list

	&__list {
		display: flex;
		justify-content: center;
		gap: toRem(20);
		flex-wrap: wrap;
		@media (max-width: $mobileSmall) {
			gap: toRem(17);
		}
	}
}

.item-advantage {
	background-color: var(--white-color);
	border-radius: toRem(14.2);
	padding: toRem(31) toRem(36) toRem(35) toRem(36);
	flex: 0 0 calc(33.333% - toRem(20));
	@media (max-width: toEm(1200)) {
		flex: 0 0 40%;
	}
	@media (max-width: $mobile) {
		flex: 0 0 100%;
	}

	// .item-advantage__icon

	&__icon {
		flex: 0 0 toRem(52);
		width: toRem(52);
		height: toRem(52);
		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
		@media (max-width: $mobileSmall) {
			flex: 0 0 toRem(44);
			width: toRem(44);
			height: toRem(44);
		}
	}

	// .item-advantage__title

	&__title {
		&:not(:last-child) {
			margin-bottom: toRem(20);
			@media (max-width: $mobile) {
				margin-bottom: toRem(10);
				display: inline-block;
			}
		}
		// font-size: toRem(24);
		@include adaptiveValue("font-size", 24, 20, 0, $containerWidth, 1200);
		font-weight: 600;
		line-height: 1.2;
		text-transform: capitalize;
	}

	// .item-advantage__text

	&__text {
		line-height: 1.5;
		letter-spacing: toRem(0.3);
		@media (max-width: $mobileSmall) {
			font-size: toRem(14);
		}
	}
}
