.block {
	padding-top: toRem(123);
	padding-bottom: toRem(123);
	background-color: var(--green-color-100);
	@media (max-width: $mobile) {
		padding-bottom: toRem(80);
		padding-top: toRem(50);
	}
	// .block__container

	&__container {
		display: grid;

		// @include adaptiveValue("gap", 80, 120, 0, $containerWidth, 767.98);
		gap: toRem(80);

		@media (min-width: $mobile) {
			grid-template-columns: 1fr 42.44%;
			align-items: center;
		}
		@media (max-width: $mobile) {
			gap: toRem(50);
		}
	}

	// .block__content

	&__content {
		&.content {
			.content__title {
				@include adaptiveValue("font-size", 44, 30, 0, $containerWidth, 767.98);
				@media (max-width: $mobileSmall) {
					font-size: toRem(24);
				}
			}

			.content__text {
				@include adaptiveValue("font-size", 20, 16, 0, $containerWidth, 767.98);
				@media (max-width: $mobileSmall) {
					font-size: toRem(14);
				}
			}
		}
	}

	// .block__image-wrapper

	&__image-wrapper {
		position: relative;
		&::before,
		&::after {
			content: "";
			position: absolute;
			width: 100%;
			height: 100%;
			border-radius: 30% 0 0 0;
			z-index: 1;
			@media (max-width: $mobile) {
				width: 82%;
				border-radius: 20% 0 0 0;
			}
		}
		&::before {
			top: 0;
			left: 0;
			top: toRem(-38);
			left: toRem(-38);
			background-color: var(--yellow-color);
			@media (max-width: $mobile) {
				right: toRem(20);
				top: toRem(-20);
				left: initial;
			}
		}
		&::after {
			background-color: var(--green-color-150);
			bottom: toRem(-38);
			right: toRem(-38);
			@media (max-width: $mobile) {
				right: toRem(-38);
				bottom: toRem(-20);
			}
		}
		@media (max-width: $mobile) {
			transform: translateX(toRem(-40));
		}
	}

	// .block__image

	&__image {
		overflow: hidden;
		padding-bottom: percent(542, 519);
		position: relative;
		z-index: 2;
		@media (min-width: $mobile) {
			border-radius: 30% 0 0 0;
		}
		@media (max-width: $mobile) {
			padding-bottom: 76.431599%;
			margin-left: auto;
			width: 82%;
		}
		@media (max-width: $mobile) {
			border-radius: 20% 0 0 0;
		}
	}

	&_main {
		@media (max-width: $mobile) {
			padding-top: toRem(40);
			padding-bottom: toRem(80);
		}
		.block__content {
			&.content {
				.content__title {
					@include adaptiveValue("font-size", 47, 29);
				}
			}
		}
	}
	&_bg {
		background-color: var(--white-color);
	}
	&_reverse {
		.block__container {
			@media (min-width: $mobile) {
				grid-template-columns: 42.44% 1fr;
			}
		}
		.block__image {
			border-radius: 0 20% 0 0;
			@media (max-width: $mobile) {
			}
		}
		.block__image-wrapper {
			@media (max-width: $mobile) {
				transform: translateX(toRem(-20));
			}
			&::before,
			&::after {
				border-radius: 0 20% 0 0;
				@media (max-width: $mobile) {
				}
			}
			order: 1;
			&::before {
				right: initial;
				left: toRem(37);
				@media (max-width: $mobile) {
					right: toRem(-20);
					left: initial;
				}
			}
			&::after {
				right: toRem(38);
				@media (max-width: $mobile) {
					right: toRem(20);
				}
			}
		}
		.block__content {
			@media (min-width: $mobile) {
				order: 2;
			}
		}
	}
}
