.programs {
	color: var(--white-color);
	position: relative;
	padding-bottom: toRem(60);
	@media (max-width: $mobile) {
		padding-bottom: toRem(37);
	}
	&::before {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background-color: rgba(0, 0, 0, 0.85);
		z-index: 1;
	}
	// .programs__container

	&__container {
		position: relative;
		z-index: 2;
	}

	// .programs__content

	&__content {
		border-radius: 0 0 toRem(15) toRem(15);
		background-color: var(--brand-color);
		max-width: toRem(836);
		margin: 0 auto;
		padding: toRem(30) toRem(38);
		@media (max-width: $mobile) {
			padding: toRem(15);
		}
		&.content {
			.content__title {
				color: var(--white-color);
				@include adaptiveValue("font-size", 40, 24);
			}
		}
		&:not(:last-child) {
			margin-bottom: toRem(36);
			@media (max-width: $mobileSmall) {
				margin-bottom: toRem(20);
			}
		}
	}

	// .programs__items

	&__items {
		display: grid;
		align-items: start;
		position: relative;
		gap: toRem(107);
		@media (min-width: $mobile) {
			grid-template-columns: repeat(2, 1fr);
			max-width: toRem(836);
			margin: 0 auto;
			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 50%;
				width: toRem(1);
				height: 100%;
				background-color: var(--white-color);
			}
		}
		@media (max-width: $mobile) {
			gap: toRem(20);
		}
	}

	// .programs__item

	&__item {
	}

	// .programs__image

	&__image {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
		z-index: -1;
	}
}

.item-program {
	display: grid;
	align-items: center;
	justify-items: center;
	&:nth-child(odd) {
		ul {
			max-width: toRem(407);
		}
	}
	&:nth-child(even) {
		ul {
			max-width: toRem(380);
		}
	}
	@media (min-width: $mobile) {
		// &:nth-child(odd) {
		// 	padding-right: toRem(60);
		// 	max-width: toRem(407);
		// 	margin-left: auto;
		// }
		// &:nth-child(even) {
		// 	padding-left: toRem(60);
		// 	max-width: 19.6875rem;
		// 	margin-left: 20%;
		// }
	}

	// .item-program__title

	&__title {
		@include adaptiveValue("font-size", 24, 20, 0, $containerWidth, 767.98);
		font-weight: 600;
		display: inline-block;
		&:not(:last-child) {
			margin-bottom: toRem(20);
		}
		&.title-decor {
			&::after {
				margin-top: toRem(5);
			}
		}
	}
	ul {
		display: grid;
		justify-items: center;
		gap: toRem(20);
		text-align: center;
		margin: 0 auto;
		@media (max-width: $mobile) {
			justify-items: center;
			gap: toRem(10);
		}
		li {
			line-height: 1.5;
			// display: flex;
			// gap: toRem(5);
			@media (max-width: $mobileSmall) {
				font-size: toRem(14);
			}
			&::before {
				content: "";
				display: inline-block;
				position: relative;
				top: toRem(-2);
				right: toRem(10);
				width: toRem(5);
				flex: 0 0 toRem(5);
				height: toRem(5);
				border-radius: 50%;
				background-color: var(--white-color);
				@media (max-width: $mobileSmall) {
				}
			}
		}
	}

	@media (max-width: $mobile) {
		&:not(:last-child) {
			&::after {
				content: "";
				width: 100%;
				height: toRem(0.5);
				margin-top: toRem(20);
				background-color: var(--white-color);
			}
		}
	}
}
