.testimonials {
	background-color: var(--green-color-100);
	padding-top: toRem(70);
	padding-bottom: toRem(36);
	@media (max-width: $mobile) {
		padding-top: toRem(50);
		padding-bottom: toRem(26);
	}

	// .testimonials__container

	&__container {
	}

	// .testimonials__content

	&__content {
		text-align: left;
		max-width: toRem(600);
		&:not(:last-child) {
			margin-bottom: toRem(30);
			@media (max-width: $mobile) {
				margin-bottom: toRem(10);
			}
		}
		&.content {
			.content__title {
				line-height: 1.2;
				&:not(:last-child) {
					margin-bottom: toRem(7);
				}
			}
		}
	}

	// .testimonials__body

	&__body {
	}

	// .testimonials__slider

	&__slider {
		padding: toRem(20);
		margin: 0 toRem(-20);
	}

	// .testimonials__wrapper

	&__wrapper {
	}
}

.testimonial-slide {
	padding: toRem(35) toRem(0) toRem(45) toRem(0);
	background-color: var(--white-color);
	box-shadow: 0 toRem(4) toRem(15) 0 rgba(0, 0, 0, 0.1);
	border-radius: toRem(15);
	display: flex;
	flex-direction: column-reverse;
	gap: toRem(42);
	@include adaptiveValue("padding-right", 52, 20, 0, $containerWidth, 991.98);
	@include adaptiveValue("padding-left", 52, 20, 0, $containerWidth, 991.98);
	@media (max-width: $tablet) {
		padding-top: toRem(40);
		padding-bottom: toRem(40);
	}
	@media (max-width: $mobile) {
		gap: toRem(20);
	}
	@media (max-width: $mobileSmall) {
		padding: toRem(20) toRem(22);
	}
	// .testimonial-slide__quote

	&__quote {
		font-size: toRem(15);
		line-height: 1.5;
		@media (max-width: $mobileSmall) {
			font-size: toRem(12);
		}
	}

	// .testimonial-slide__profile

	&__profile {
	}
}

.profile {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: toRem(20);
	&::after {
		content: "";
		flex: 0 0 toRem(52);
		width: toRem(52);
		height: toRem(52);
		background: url("@img/icons/quote.png") 0 0 / contain no-repeat;
		@media (max-width: $mobileSmall) {
			flex: 0 0 toRem(28);
			width: toRem(28);
			height: toRem(28);
		}
	}

	&__wrapper {
		display: flex;
		align-items: center;
		gap: toRem(20);
		@media (max-width: $mobileSmall) {
			gap: toRem(10);
		}
	}
	// .profile__avatar

	&__avatar {
		flex: 0 0 toRem(50);
		width: toRem(50);
		height: toRem(50);
		border-radius: 50%;
		@media (max-width: $mobileSmall) {
			flex: 0 0 toRem(40);
			width: toRem(40);
			height: toRem(40);
		}
	}

	// .profile__name

	&__name {
		font-weight: 700;
		font-size: toRem(15.5);
		line-height: 1.3;
		@media (max-width: $mobileSmall) {
			font-size: toRem(14);
		}
	}

	// .profile__profession
}
