.footer {
	padding-top: toRem(70);
	padding-bottom: toRem(55);
	color: var(--white-color);
	position: relative;
	&::before {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background-color: var(--green-color-400);
		z-index: 1;
	}
	@media (max-width: $mobile) {
		padding-top: toRem(32);
		padding-bottom: toRem(32);
	}

	// .footer__container

	&__container {
		position: relative;
		z-index: 2;
		display: grid;

		gap: toRem(20);
		@media (min-width: $mobile) {
			grid-template-columns: 1fr 49.95%;
		}
	}

	// .footer__content

	&__content {
		text-align: left;
		&:not(:last-child) {
			@media (max-width: $mobile) {
				text-align: center;
			}
		}

		&.content {
			.content__title {
				@media (min-width: $mobile) {
					max-width: toRem(400);
				}
				@media (max-width: $mobile) {
					display: inline-block;
				}
				&::after {
					background-color: var(--white-color);
					height: toRem(1);
					margin-top: toRem(15);
					width: 56%;
					@media (max-width: $mobile) {
						width: 100%;
					}
				}
			}

			.content__text {
				font-size: toRem(16);

				@media (max-width: $mobileSmall) {
					font-size: toRem(14);
				}
			}
		}
	}

	&__bg {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
		z-index: -1;
		object-position: top;
		@media (max-width: $mobile) {
			object-position: 30%;
		}
	}
}
