.journey {
	padding-top: toRem(70);
	padding-bottom: toRem(70);
	@media (max-width: $mobile) {
		padding-top: toRem(50);
		padding-bottom: toRem(50);
	}
	// .journey__container

	&__container {
		max-width: toRem(1265);
		display: grid;

		align-items: center;
		@include adaptiveValue("gap", 68, 30, 0, $containerWidth, 991.98);
		@media (min-width: $tablet) {
			grid-template-columns: 41.143% 1fr;
		}
		@media (max-width: $mobile) {
			gap: toRem(15);
		}
	}

	// .journey__content

	&__content {
		&.content {
			.content__title {
				@include adaptiveValue("font-size", 42, 24);
			}

			.content__text {
			}
		}
	}

	// .journey__items

	&__items {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		gap: toRem(18);
		@media (max-width: toEm(600)) {
			gap: toRem(10) toRem(8);
		}
	}
}
.item-journey {
	border-radius: toRem(20);
	border: toRem(1) solid rgba(0, 0, 0, 0.1);
	box-shadow: 0 toRem(7) toRem(17) 0 rgba(43, 171, 160, 0.09);
	padding: toRem(23) toRem(25);
	flex: 0 0 calc(50% - 1.125rem);
	@media (max-width: toEm(1024)) {
		padding: toRem(20) toRem(15);
	}
	@media (max-width: $mobile) {
		flex: 0 0 calc(51% - 1.125rem);
	}
	@media (max-width: toEm(600)) {
		padding: toRem(12);
		flex: 0 0 calc(51% - 10px);
	}
	// .item-journey__icon
	&__icon {
		flex: 0 0 toRem(70);
		width: toRem(70);
		height: toRem(70);
		&:not(:last-child) {
			margin-bottom: toRem(20);
			@media (max-width: $mobile) {
				margin-bottom: toRem(7);
			}
		}
		@media (max-width: $mobile) {
			flex: 0 0 toRem(32);
			width: toRem(32);
			height: toRem(32);
		}
	}

	// .item-journey__title

	&__title {
		&:not(:last-child) {
			margin-bottom: toRem(13);
			@media (max-width: toEm(600)) {
				margin-bottom: toRem(4);
			}
		}
		color: #293039;
		font-size: toRem(17);
		font-style: normal;
		font-weight: 700;
		line-height: 1.3;
		@media (max-width: toEm(600)) {
			font-size: toRem(11);
		}
	}

	// .item-journey__text

	&__text {
		line-height: 1.45;
		font-size: toRem(16);
		@media (max-width: toEm(600)) {
			font-size: toRem(9);
		}
	}
}
