//<BUTTONS (a.btn+tab)>
// Сніппет (HTML): btn

.button {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	padding: toRem(20.5) toRem(32);
	background-color: var(--brand-color);
	font-size: toRem(18);
	border-radius: toRem(50);
	color: var(--white-color);
	transition: background-color 0.3s;
	&_border {
		border-radius: toRem(10);
		border: toRem(1) solid var(--white-color);
		box-shadow: 0 toRem(1) toRem(11) 0 rgba(0, 0, 0, 0.15);
	}
	@media (max-width: $mobileSmall) {
		font-size: toRem(10);
		padding: toRem(11) toRem(13);
		font-weight: 400;
	}
	@media (any-hover: hover) {
		&:hover {
			background-color: var(--hover--color);
		}
	}
}
